.material_wrap{
    height: 60%;
    width: 50%;
    float: left;
    border-bottom: 2px solid #000;
    border-right: 2px solid #000;
}

.material_header{
    height: 50px;
    border-bottom: 1px solid #000; 
    .material_index{
        width: 50px;
        height: 50px;
        color:#fff;
        line-height: 50px;
        border-right: 1px solid #000;
        font-size: 30px;
        text-align: center;
        float: left;
    }
    .material_login{
        width: 150px;
        height: 50px;
        color:#fff;
        line-height: 50px;
        font-size: 14px;
        text-align:left;
        float: left;
        margin-left: 10px;
    }
    .material_login i{
        margin-left: 10px; 
    }
    .material_tabs{
        width: 250px;
        height: 50px;
        color:#888;
        line-height: 50px;
        float: right; 
    }
    .material_tabs li{
        float: left;
        width: 50px;
        height: 50px;
        font-size: 14px;
        text-align:center;
    }
    .material_tabs li.active{
        background: #000;
        color:#fff;
    }
}


.material_body{
    width: 100%;
    height: calc(100% - 50px);
    .material_video_wrap{
        width:100%;
        height: 100%;
        .material_video_form{
            height: 40px;
            width: 100%;
            border-bottom: 1px solid #000;
            input{
                height: 20px;
                width:200px;
                line-height: 20px;
                margin: 7px;
                background: #000;
                color:#fff;
                border:none;
                border-radius:4px;
                padding: 3px;
                margin-left: 20px;
                font-size: 12px;
                text-indent: 4px;
            }
        }
    }
}





